@import url(//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-theme.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Title {
  background-color: #63bbff;
  justify-self: start;
  padding-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 3rem;
  align-items: center;
}

.homepage {
  flex-direction: column;
  width: 320px;
  height: 300px;
  margin: 1%;
  padding: 1%;
}

.flag {
  background-position: "center";
  background-size: "cover";
  background-repeat: "no-repeat";
  width: 200px;
  height: 130px;
}

.continent-Box {
  margin: 1%;
}

.Continent-Title {
  background-color: #63bbff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 3rem;
  align-items: center;
}

.flexed {
  display: flex;
}

.countryBox {
  text-align: left;
}

.country {
  background-color: transparent;
  border: transparent;
  box-shadow: 0 2px 50px rgba(51, 0, 254, 0.3),
    0 0 20px rgba(0, 0, 0, 0.1) inset;
}

.selectedCountry {
  background-color: transparent;
  border: transparent;
  box-shadow: 0 2px 70px rgba(51, 0, 254, 0.3), 0 0 2px rgb(255, 255, 255) inset;
  border-radius: 20px;
}

.continent {
  flex-direction: column;
}

#city1Col {
  width: 90%;
}

#city2Col {
  width: 140%;
}

.cityBoxBorder {
  border: 2px dashed #f7eeee;
  padding: 2%;
}

.cityBox {
  padding: 10px;
  float: left;
  background-color: white;
  margin: 25px -85px;
  border-radius: 5px;
  background-color: #432aae;
  position: relative;
  box-shadow: 0 20px 14px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
  border-radius: 5px;
  color: #fff;
  font-size: small;
}

.city {
  text-align: left;
  font-family: "Didact Gothic", sans-serif;
  font-weight: normal;
  margin: 1% 10%;
}

.moreLink {
  text-align: right;
  font-size: small;
}

.previous {
  background-position: "center";
  background-size: "cover";
  background-repeat: "no-repeat";
  width: 100px;
  height: 200px;
  margin: 20px;
}

.previous:hover {
  background-image: "./images/arrawHover.gif";
  width: 120px;
}

.countryFlag {
  width: 300px;
  height: 200px;
  background-position: "center";
  background-size: "cover";
  background-repeat: "no-repeat";
  margin: 20px;
}

.cityListBox {
  padding: 10px;
  margin: 3% 5%;
  color: black;
  position: relative;
  font-size: larger;
  box-shadow: 0 20px 14px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
  border-radius: 5px;
}

.continentList {
  display: none;
}

@media (max-width: 500px) {
  .Title,
  .Continent-Title {
    font-size: 1rem;
    padding-left: 2px;
  }

  .homepage {
    flex-direction: row;
    width: 70%;
    height: 50%;
    margin: 1%;
    padding: 1%;
  }
  .flexed {
    display: contents;
  }
  .flag{
    flex-direction: row;
    margin-left: -20%;
  }
  .countryFlag , .flag{
    width: 60%;
    height: 50%;
  }
  .previous {
    width: 10%;
    height: 10%;
  }
  .continentName {
    display: none;
  }
  .continentList {
    display: block;
  }
  #city2Col,
  #city1Col {
    width: 90%;
  }

  .cityBox,
  .moreLink,
  .cityBoxBorder {
    padding: 2%;
    margin: 2%;
    font-size: xx-small;
  }
  .city {
    margin: 1%;
  }

  .capitals{
    font-size: xx-small;
    text-align: left;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.game {
  /* border: 1px solid #06fefe; */
  flex-direction: column;
  margin: 2%;
  padding: 10%;
  width: 300px;
}

.content-box {
  border: 2px solid black;
  padding: 2px;
}

.mainbody {
  padding: 50px;
}

.box-spacing {
  margin-bottom: 20px;
}

.App-body {
  background-color: #7bc6ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
    
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      background-color: #fff;
      color: #242424;
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #242424;
      transition: 250ms;
    }
  }
  
